import React, { Component } from "react";
import { connect } from "react-redux";
import { Field, reduxForm } from "redux-form";
import { gagal } from "../../Alert";
import validasiSaldoAwalPiutang from "../../Validations/validasiSaldoAwalPiutang";
import { renderField } from "../../renderField";
import { AxiosMasterGet } from "../../Axios";

class FormSaldoAwalPiutang extends Component {
  constructor(props) {
    super(props);
    this.state = {
      listSupplier: [],
    };
  }

  componentDidMount() {
    AxiosMasterGet("customers")
      .then((res) =>
        this.setState({
          listSupplier: res.data,
        })
      )
      .catch(() =>
        gagal("Sepertinya koneksi bermasalah, mohon periksa koneksi anda")
      );
  }
  render() {
    return (
      <form onSubmit={this.props.handleSubmit}>
        <div className="row">
          <div className="col-lg-3">
            <label htmlFor="">Nama Toko</label>
            <Field name="namaToko" component="select" className="form-control">
              <option value="DEFAULT" key="DEFAULT">
                SILAHKAN PILIH
              </option>
              {this.state.listSupplier.map((list) => (
                <option
                  value={list.kodeCustomer + "|" + list.namaToko}
                  key={list.kodeCustomer + "|" + list.namaToko}
                >
                  {list.namaToko}
                </option>
              ))}
            </Field>
          </div>
          <div className="col-lg-3">
            <Field
              label="Detail"
              name="detail"
              component={renderField}
              type="text"
            />
          </div>
          <div className="col-lg-3">
            <Field
              label="Tanggal Faktur"
              name="tglFaktur"
              component={renderField}
              type="date"
            />
          </div>
          <div className="col-lg-3">
            <Field
              label="Nomor Bon"
              name="noBon"
              component={renderField}
              type="text"
            />
          </div>
          <div className="col-lg-3">
            <Field
              label="Bruto(Gr)"
              name="bruto"
              component={renderField}
              type="text"
            />
          </div>
          <div className="col-lg-3">
            <Field
              label="Netto(Gr)"
              name="netto"
              component={renderField}
              type="text"
            />
          </div>
          <div
            className={
              this.props.tipe === "TITIP" ? "col-lg-3 d-none" : "col-lg-3"
            }
          >
            <Field
              label="Saldo(Gr)"
              name="totalSaldoGr"
              component={renderField}
              type="text"
            />
          </div>
          <div
            className={
              this.props.tipe === "TITIP" ? "col-lg-3 d-none" : "col-lg-3"
            }
          >
            <Field
              label="saldo(Rp)"
              name="totalSaldoRp"
              component={renderField}
              type="text"
            />
          </div>
          <div
            className={
              this.props.tipe === "HUTANG" ? "col-lg-3 d-none" : "col-lg-3"
            }
          >
            <Field
              label="Titip(Rp)"
              name="totalTitipRp"
              component={renderField}
              type="text"
            />
          </div>
          <div
            className={
              this.props.tipe === "HUTANG" ? "col-lg-3 d-none" : "col-lg-3"
            }
          >
            <Field
              label="Titip(Gr)"
              name="totalTitipGr"
              component={renderField}
              type="text"
            />
          </div>
          <div className="col-lg-12">
            <div className="text-right">
              <button className="btn btn-primary" type="submit">
                Simpan
              </button>
            </div>
          </div>
        </div>
      </form>
    );
  }
}

FormSaldoAwalPiutang = reduxForm({
  form: "FormSaldoAwalPiutang",
  enableReinitialize: true,
  validate: validasiSaldoAwalPiutang,
})(FormSaldoAwalPiutang);
export default connect()(FormSaldoAwalPiutang);
